<template>
    <button class="arrow-button" :class="direction" @click="handleClick">
      <slot></slot>
    </button>
  </template>
  
  <script>
  export default {
    name: "ArrowButton",
    props: {
      direction: {
        type: String,
        required: true, // "prev" 或 "next"
      },
    },
    methods: {
      handleClick() {
        this.$emit("click"); // 触发父组件的点击事件
      },
    },
  };
  </script>
  
